import luchRequest from '@/components/luch-request' // npm
import config from '@/config.js'
import errorCode from '@/common/request/errorCode'
import { getToken, getRefreshToken, setToken, setRefreshToken } from '@/common/utils/auth'
import { toast } from '@/common/utils/common'
import store from '@/store'
import request from '@/common/request/request.js'

// luch-requesthttps://www.quanzhan.co/luch-request/guide/3.x/#%E5%85%A8%E5%B1%80%E8%AF%B7%E6%B1%82%E9%85%8D%E7%BD%AE
// luchRequest
const http = new luchRequest({
  baseURL: config.baseUrl,
  timeout: 20000, // 
  dataType: 'json',
  custom: {
    loading: true
  }
})

// 
http.interceptors.request.use(
  (config) => {
    // 
    let token = getToken()
    if (token) {
      // token
      config.header['Authorization'] = token
    }
    return config
  },
  (error) => {
    return Promise.reject(error)
  }
)

// 
http.interceptors.response.use(
  (res) => {
    // console.log("", res)
    // 401
    const code = res.data.code || 200
    // 
    const msg = res.data.message || errorCode[code] || errorCode['default']
    // token
    const refresh = res.header['if-refresh']
    if (code === 500) {
      toast(msg)
      return Promise.reject(new Error(msg))
    } else if (code === 401) {
      // tokentoken
      refreshToken().then((res) => {
        setToken(`${res.data.token_type} ${res.data.access_token}`)
        setRefreshToken(res.data.refresh_token)
      })
      toast('')
      return Promise.reject('error')
    } else if (code !== 200) {
      toast(msg)
      return Promise.reject('error')
    } else if (code === 200) {
      if (refresh === '1') {
        // tokentoken
        refreshToken().then((res) => {
          setToken(`${res.data.token_type} ${res.data.access_token}`)
          setRefreshToken(res.data.refresh_token)
        })
      }
      return res.data
    } else {
      return res.data
    }
  },
  (error) => {
    console.log('err', error)
    let message = error.data.message || error.errMsg
    const status = error.statusCode
    switch (status) {
      case 400:
        message = ''
        break
      case 401:
        // token
        store.dispatch('auth/LogOut')
        message = ''
        break
      case 403:
        message = ''
        break
      case 404:
        message = ''
        break
      case 408:
        message = ''
        break
      case 500:
        message = ''
        break
      case 501:
        message = ''
        break
      case 502:
        message = ''
        break
      case 503:
        message = ''
        break
      case 504:
        message = ''
        break
      case 505:
        message = 'HTTP'
        break
      default:
        break
    }
    toast(message)
    return Promise.reject(error)
  }
)

//  token
function refreshToken() {
  const data = JSON.stringify(getRefreshToken())
  return request.post('/auth/token/refresh/', data)
}

export default http
