import { getToken } from '@/common/utils/auth'
import store from '@/store'
import { RouterMount, createRouter } from 'uni-simple-router'

// uni-simple-router https://www.hhyang.cn/v2/start/cross/codeRoute.html

// 
const loginPage = '/pages/login/login'
// 
const indexPage = '/pages/index'

const router = createRouter({
  platform: process.env.VUE_APP_PLATFORM,
  detectBeforeLock: (router, to, navType) => {
    if (navType === 'replaceAll' && (to.path === loginPage || to.path === indexPage)) {
      router.$lockStatus = false // 
    }
  },
  routes: [...ROUTES] // ROUTESwebpackdefaultPlugin
})

//
router.beforeEach((to, from, next) => {
  if (to.meta.loginAuth) {
    // 
    if (getToken()) {
      if (!store.state.auth.isUser) {
        store.dispatch('auth/GetInfo')
      }
      if (to.path === loginPage) {
        next({
          path: indexPage,
          NAVTYPE: 'replaceAll'
        })
      }
      next()
    } else {
      next({
        path: loginPage,
        NAVTYPE: 'replaceAll'
      })
    }
  } else if (to.path === loginPage && getToken()) {
    // token
    next({
      path: indexPage,
      NAVTYPE: 'replaceAll'
    })
  } else {
    // 
    next()
  }
})

// 
router.afterEach((to, from) => {
  // console.log('')
})

export { router, RouterMount }
